<template>
    <div class="main-bg-color card-info-container">
        {{ title }}
        <van-button
            v-if="showButton"
            :type="buttonType"
            plain
            size="mini"
            class="right-button"
            round
            >{{ buttonName }}</van-button
        >
        <div
            v-if="showAllButton"
            class="right-button"
            style="display: flex; align-items: center"
        >
            <van-button type="primary" size="mini" @click="$emit('checkAll')"
                >全选</van-button
            >
            <van-button
                type="primary"
                plain
                size="mini"
                @click="$emit('toggleAll')"
                >反选</van-button
            >
        </div>
    </div>
</template>

<script>
export default {
    props: {
        title: {
            type: String,
        },
        showButton: {
            type: Boolean,
        },
        showAllButton: {
            type: Boolean,
        },
        buttonName: {
            type: String,
        },
        buttonType: {
            type: String,
        },
    },
};
</script>

<style scoped>
.card-info-container {
    border-radius: 10px 10px 0 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    box-sizing: border-box;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>