<template>
    <div class="top-nav-container">
        <van-nav-bar
            :title="activeRouterName"
            left-arrow
            @click-left="goToRouter"
        />
    </div>
</template>

<script>
export default {
    props: {
        activeRouterName: {
            type: String,
        },
        originRouterName: {
            type: String,
        },
        goBack: {
            type: Boolean,
        },
    },
    methods: {
        goToRouter() {
            if (this.goBack) {
              this.$router.go(-1)
            } else {
                this.$router.push({
                    name: this.originRouterName,
                });
            }
        },
    },
};
</script>

<style  scoped>
.top-nav-container {
}
</style>